export default function Home() {
  const { t } = useLanguage();

  const [ref, inView] = useInView({ threshold: 0.55, triggerOnce: true });
  const [startCounting, setStartCounting] = useState(false);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isQuoteOpen, setIsQuoteOpen] = useState(false);
  const [showFloatingButton, setShowFloatingButton] = useState(false);
  const [currentTestimonial, setCurrentTestimonial] = useState(0);
  const [selectedCover, setSelectedCover] = useState(null);

  useEffect(() => {
    if (inView) setStartCounting(true);
  }, [inView]);

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentIndex((prev) => (prev + 1) % heroImages.length);
    }, 6000);
    return () => clearInterval(interval);
  }, []);

  useEffect(() => {
    const handleScroll = () => {
      setShowFloatingButton(window.scrollY > window.innerHeight * 0.4);
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentTestimonial((prev) => (prev + 1) % testimonials.length);
    }, 8000);
    return () => clearInterval(interval);
  }, []);

  return (
    <main className="min-h-screen bg-gradient-to-b from-gray-50 to-white antialiased">
      {/* Hero */}
      <section className="relative h-screen w-full flex items-center justify-center overflow-hidden mt-[-1px]">
        {heroImages.map((src, index) => (
          <motion.div
            key={src}
            className="absolute inset-0 bg-cover bg-center bg-no-repeat"
            style={{ backgroundImage: `url(${src})` }}
            initial={{ opacity: 0 }}
            animate={{ opacity: index === currentIndex ? 1 : 0 }}
            transition={{ duration: 1.8, ease: "easeInOut" }}
          />
        ))}
        <div className="absolute inset-0 bg-gradient-to-r from-blue-950/45 to-gray-950/45 z-10 pointer-events-none" />
        <div className="relative z-20 text-center px-6 max-w-5xl mx-auto text-white pt-40 md:pt-48">
          <motion.h1
            initial={{ opacity: 0, y: 40 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 1, ease: "easeOut" }}
            className="text-5xl md:text-7xl font-extrabold tracking-tight mb-6 drop-shadow-lg"
          >
            {t("slogan")}
          </motion.h1>
          <motion.p
            initial={{ opacity: 0, y: 40 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 1, delay: 0.3, ease: "easeOut" }}
            className="text-xl md:text-3xl font-light mb-12 drop-shadow-md"
          >
            {t("heroSubtitle")}
          </motion.p>

          <div className="flex flex-col sm:flex-row items-center justify-center gap-6">
            <a
              href="https://wa.me/27727001800?text=Hi%2C%20I%20need%20urgent%20assistance%20please"
              className="bg-green-600 hover:bg-green-700 text-white font-semibold py-4 px-12 rounded-xl text-lg shadow-xl transition-all hover:scale-105"
            >
              {t("emergency")}
            </a>
            <button
              onClick={() => setIsQuoteOpen(true)}
              className="bg-blue-900 hover:bg-blue-800 text-white font-semibold py-4 px-12 rounded-xl text-lg shadow-xl transition-all hover:scale-105"
            >
              {t("quote")}
            </button>
          </div>
        </div>
      </section>

      {/* Milestones */}
      <section ref={ref} className="py-24 bg-white">
        <div className="max-w-7xl mx-auto px-6 lg:px-8">
          <h2 className="text-4xl md:text-5xl font-bold text-center text-gray-900 mb-16">
            {t("legacyTitle")}
          </h2>

          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-12">
            <motion.div initial={{ opacity: 0, y: 30 }} animate={{ opacity: 1, y: 0 }} transition={{ duration: 0.8 }} className="text-center">
              <MapPin className="w-16 h-16 mx-auto text-blue-900 mb-6" />
              <div className="text-5xl font-bold text-blue-900 mb-2">
                {startCounting ? <CountUp end={30} duration={2.5} suffix="+" /> : "0"}
              </div>
              <p className="text-xl text-gray-600">{t("branches")}</p>
            </motion.div>

            <motion.div initial={{ opacity: 0, y: 30 }} animate={{ opacity: 1, y: 0 }} transition={{ duration: 0.8, delay: 0.2 }} className="text-center">
              <Trophy className="w-16 h-16 mx-auto text-blue-900 mb-6" />
              <div className="text-5xl font-bold text-blue-900 mb-2">
                {startCounting ? <CountUp end={15} duration={2.5} suffix="+" /> : "0"}
              </div>
              <p className="text-xl text-gray-600">{t("years")}</p>
            </motion.div>

            <motion.div initial={{ opacity: 0, y: 30 }} animate={{ opacity: 1, y: 0 }} transition={{ duration: 0.8, delay: 0.4 }} className="text-center">
              <HeartHandshake className="w-16 h-16 mx-auto text-blue-900 mb-6" />
              <div className="text-5xl font-bold text-blue-900 mb-2">
                {startCounting ? <CountUp end={1000000} duration={2.5} suffix="+" /> : "0"}
              </div>
              <p className="text-xl text-gray-600">{t("families")}</p>
            </motion.div>

            <motion.div initial={{ opacity: 0, y: 30 }} animate={{ opacity: 1, y: 0 }} transition={{ duration: 0.8, delay: 0.6 }} className="text-center">
              <Calendar className="w-16 h-16 mx-auto text-blue-900 mb-6" />
              <div className="text-5xl font-bold text-blue-900 mb-2">
                {startCounting ? <CountUp end={24} duration={2.5} suffix="/7" /> : "0"}
              </div>
              <p className="text-xl text-gray-600">{t("support")}</p>
            </motion.div>
          </div>
        </div>
      </section>

      {/* Featured Covers */}
      <section className="py-20 bg-gradient-to-b from-blue-50 to-gray-50">
        <div className="max-w-7xl mx-auto px-6 lg:px-8">
          <h2 className="text-4xl md:text-5xl font-bold text-center text-blue-900 mb-6">
            {t("coversTitle")}
          </h2>
          <p className="text-xl text-center text-gray-600 mb-16 max-w-4xl mx-auto">
            {t("coversSubtitle")}
          </p>

          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-8">
            {covers.map((cover, index) => (
              <motion.div
                key={cover.id}
                initial={{ opacity: 0, y: 30 }}
                whileInView={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.6, delay: index * 0.1 }}
                viewport={{ once: true }}
                className="bg-white rounded-2xl shadow-md overflow-hidden border border-blue-100 hover:shadow-2xl hover:border-blue-300 transition-all duration-300 cursor-pointer"
              >
                <div className="relative h-48 overflow-hidden">
                  <img src={cover.image} alt={`${cover.subtitle} for ${cover.title}`} className="w-full h-full object-cover transition-transform duration-500 hover:scale-105" />
                  <div className="absolute top-2 right-2 bg-blue-900 text-white px-3 py-1 rounded-full text-sm font-semibold shadow-md">
                    {cover.id}
                  </div>
                </div>
                <div className="p-6 text-center">
                  <h3 className="text-2xl font-bold text-blue-900">{cover.title}</h3>
                  <p className="text-lg font-medium text-gray-700 mt-1">{cover.subtitle}</p>
                  <p className="text-sm text-gray-500 italic mt-2">{cover.tagline}</p>
                  <p className="text-xl font-bold text-green-700 mt-3">
                    {t("fromPrice", { price: cover.price })}
                  </p>
                  <button
                    onClick={() => setSelectedCover(cover)}
                    className="mt-4 bg-blue-600 hover:bg-blue-700 text-white font-semibold py-2 px-6 rounded-lg shadow-md transition-all hover:scale-105"
                  >
                    {t("seeMore")}
                  </button>
                </div>
              </motion.div>
            ))}
          </div>

          {/* Additional Benefits */}
          <div className="mt-16 bg-white rounded-2xl p-8 md:p-12 text-center border border-blue-200 shadow-lg">
            <h3 className="text-3xl font-bold text-blue-900 mb-6">
              {t("benefitsTitle")}
            </h3>
            <div className="flex flex-col md:flex-row justify-center gap-8 max-w-4xl mx-auto">
              <div className="flex items-center gap-4">
                <div className="text-5xl text-green-600">üöö</div>
                <p className="text-lg text-gray-800">{t("repatriation")}</p>
              </div>
              <div className="flex items-center gap-4">
                <div className="text-5xl text-green-600">‚öïÔ∏è</div>
                <p className="text-lg text-gray-800">{t("doctors")}</p>
              </div>
            </div>
            <p className="mt-8 text-gray-600 italic">
              Covers up to 10 members (no surname restrictions). No age limit. Immediate cover for re-joins in some cases.
            </p>
            <button
              onClick={() => setIsQuoteOpen(true)}
              className="mt-8 bg-green-600 hover:bg-green-700 text-white font-semibold py-4 px-12 rounded-xl text-lg shadow-xl transition-all hover:scale-105"
            >
              {t("quote")}
            </button>
          </div>
        </div>
      </section>

      {/* Testimonials */}
      <section className="py-24 bg-gradient-to-b from-gray-50 to-white">
        <div className="max-w-7xl mx-auto px-6 lg:px-8">
          <h2 className="text-4xl md:text-5xl font-bold text-center text-gray-900 mb-16">
            {t("testimonialsTitle")}
          </h2>

          {/* testimonial carousel remains the same */}

          <div className="text-center mt-16">
            <p className="text-lg text-gray-600 mb-6">
              {t("joinFamilies")}
            </p>
            <button
              onClick={() => setIsQuoteOpen(true)}
              className="bg-green-600 hover:bg-green-700 text-white font-semibold py-4 px-12 rounded-xl text-lg shadow-xl transition-all hover:scale-105"
            >
              {t("speakToday")}
            </button>
          </div>
        </div>
      </section>

      {/* Placeholder & Footer remain unchanged for now */}

      <QuoteModal isOpen={isQuoteOpen} onClose={() => setIsQuoteOpen(false)} />

      {selectedCover && (
        <CoverModal isOpen={!!selectedCover} onClose={() => setSelectedCover(null)} cover={selectedCover} />
      )}

      {showFloatingButton && (
        <button
          onClick={() => setIsQuoteOpen(true)}
          className="fixed bottom-6 right-6 z-50 bg-green-600 hover:bg-green-700 text-white p-5 rounded-full shadow-2xl transition-all hover:scale-110 focus:outline-none focus:ring-4 focus:ring-green-300 flex items-center justify-center"
          aria-label="Get a personalized quote"
        >
          <MessageCircle size={28} />
          <span className="sr-only">Get Quote</span>
        </button>
      )}
    </main>
  );
}